import React, { useEffect } from 'react';
import { unSetUserNavigation } from '../../../helper/GeneralHelpers';

const ChangePassword = (props) => {

    useEffect(() => {
        unSetUserNavigation()
        document.getElementById('me-change-password').classList.add("me-current-title")
    }, [])

    return (
        <div className="childWrapper">
            <div className="card">
                <div className="card-header">
                    <h3 className="card-title my-1">Change Password</h3>
                </div>
                <div className="card-body">
                    <form action="">
                        <div className="form-group">
                            <label for="exampleInputEmail1">Email address</label>
                            <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" />
                            <small id="emailHelp" className="form-text text-muted">We'll never share your email with anyone else.</small>
                        </div>
                        <div className="row">
                            <div className="col-6">
                                <div class="form-group">
                                    <label for="exampleInputPassword1">Old Password</label>
                                    <input type="password" className="form-control" id="exampleInputPassword1" />
                                </div>
                            </div>
                            <div className="col-6">
                                <div class="form-group">
                                    <label htmlFor="exampleCheck1">New Password</label>
                                    <input type="password" className="form-control" id="exampleCheck1" />
                                </div>
                            </div>
                        </div>
                        <button type="submit" className="btn float-right" style={{ backgroundColor: '#00C48D', color: '#fff' }}>
                            {/* <span className="spinner-border m-0 p-0"></span> */}
                            Submit
                        </button>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default ChangePassword;