import React from 'react';
import { useEffect } from 'react';
import { shallowEqual, useSelector } from 'react-redux';
import { unSetUserNavigation } from '../../../helper/GeneralHelpers';

const EditUser = (props) => {

    const { user } = useSelector(state => ({
        user: state.auth.currentUser.user
    }), shallowEqual)

    useEffect(() => {
        unSetUserNavigation()
        document.getElementById('me-edit-user').classList.add("me-current-title")
    }, [])

    return (
        <div className="childWrapper">
            <div className="row">
                <div className="col-12">
                    <div className="card">
                        <div className="card-header">
                            <div className="btn-group-sm">
                                <span className='card-title'>Edit Profile</span>
                                <button className="btn btn-default float-right">
                                    <i className="ri-arrow-left-fill mr-1"></i> Back
                                </button>
                            </div>
                        </div>
                        <div className="card-body">
                            <div className="row">
                                <div className="col-12">
                                    <img className="profile-image my-2" src={user && user.image} alt="User" />
                                </div>
                                <div className="col-lg-6 col-sm-12 my-1">
                                    <label htmlFor="first_name">First Name</label>
                                    <input type="text" name="first_name" value={user && user.first_name} className="form-control" />
                                </div>
                                <div className="col-lg-6 col-sm-12 my-1">
                                    <label htmlFor="last_name">Last Name</label>
                                    <input type="text" name="last_name" value={user && user.last_name} className="form-control" />
                                </div>
                                <div className="col-lg-6 col-sm-12 my-1">
                                    <label htmlFor="email">Email</label>
                                    <input type="email" name="email" disabled value={user && user.email} className="form-control" />
                                </div>
                                <div className="col-lg-6 col-sm-12 my-1">
                                    <label htmlFor="username">Username</label>
                                    <input type="text" name="username" disabled value={user && user.username} className="form-control" />
                                </div>
                            </div>
                            <button type="submit" className="btn mt-1 float-right" style={{ backgroundColor: '#00C48D', color: '#fff' }}>
                                {/* <span className="spinner-border m-0 p-0"></span> */}
                                Submit
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default EditUser;